Vue.prototype.$messageX = Vue.prototype.$message = function (message, params = {}) {

    console.log("call $message:", message);

    var {color, textColor, multiLine, icon, avatar, position, theme, timeout} = params;

    theme = !theme ? 'theme-color' : theme;

    position = !position ? "top-left" : position;
    color = !color ? theme : color;
    textColor = !textColor ? theme : textColor;
    timeout = !timeout ? 5000 : timeout;

    var flutter_inappwebview = this.flutter_inappwebview();
    if (flutter_inappwebview) {
        return flutter_inappwebview.callHandler('handler_toast', message, position, timeout);
    }


    this.$q.notify({
        color,
        textColor,
        message,
        position,
        avatar,
        multiLine,
        timeout: timeout
    })
};


Vue.prototype.$stringFormat = function stringFormat(formatted, args) {
    for (let i = 0; i < args.length; i++) {
        let regexp = new RegExp('\\{' + i + '\\}', 'gi')
        formatted = formatted.replace(regexp, args[i])
    }
    return formatted
};

