import adsobject from "@/components/script/adsobject";

export default {
  name: "banner",
  mixins: [adsobject],
  props: {
    ads_conf: undefined
  },
  data() {
    return {
      defalut_ratio: 960 / 80,
      ratio: '',
      show:true
    }
  },
  created() {
    console.log("adsbanner:", this.ads_conf);
  },
  methods: {
    on_error:function (e){
      console.log('on error:',e,this.ads_conf);
      this.show = false;
    },
    on_load: function (e) {
      console.log('ads banner image onloaed：', e);
      if (this.$ads_object.setting && this.$ads_object.setting.ratio) {
        this.ratio = eval(this.$ads_object.setting.ratio);
      } else {
        this.ratio = this.defalut_ratio;
      }
    },
    on_click_ads: function () {
      if (this.$ga) {
        this.$ga.event({
          eventCategory: 'ads_banner',
          eventAction: 'click',
          eventLabel: this.ads_conf['zone'],
          eventValue: this.ads_conf['zone']
        });
      }
    }
  }
}
