router.beforeEach((to, from, next) => {

    console.log('beforeEach:', to);

    var update = function () {
        /* 路由发生变化修改页面meta */
        if (to.meta.content) {
            let head = document.getElementsByTagName('head');
            let meta = document.createElement('meta');

            meta.content = to.meta.content;
            head[0].appendChild(meta)

            Vue.prototype.set_keywords(to.meta.content.keywords);
            Vue.prototype.set_description(to.meta.content.description);
        } else {
            Vue.prototype.set_keywords('');
            Vue.prototype.set_description('');
        }


        if (Vue.prototype.$seo_meta_conf) {
            var keywords = Vue.prototype.$seo_meta_conf['keywords'];
            Vue.prototype.add_keywords(keywords);

            var description = Vue.prototype.$seo_meta_conf['description'];
            Vue.prototype.add_description(description);
        }
    }

    update();

    // /* 路由发生变化修改页面title */
    if (to.meta.title) {
        Vue.prototype.set_title(to.meta.title);
        update();
    }
    next()
});


Vue.prototype.set_keywords = function (keywords) {
    var keywords_string = '';
    if (Array.isArray(keywords)) {
        for (var i = 0; i < keywords.length; ++i) {
            keywords_string += keywords[i] + ",";
        }
        keywords_string = keywords_string.substr(0, keywords_string.length - 1)
    } else {
        keywords_string = keywords;
    }
    if (!keywords_string) {
        keywords_string = '';
    }
    document.querySelector('meta[name="keywords"]').setAttribute('content', keywords_string);
}

Vue.prototype.padd_keywords = function (keywords) {
    this.add_keywords(keywords, true);
}

Vue.prototype.add_keywords = function (keywords, f = false) {
    var keywords_string = '';
    if (Array.isArray(keywords)) {
        for (var i = 0; i < keywords.length; ++i) {
            keywords_string += keywords[i] + ",";
        }
        keywords_string = keywords_string.substr(0, keywords_string.length - 1)
    } else {
        keywords_string = keywords;
    }
    console.log('keywords_string:', keywords_string);
    if (keywords_string) {
        var orginal_keywords = document.querySelector('meta[name="keywords"]').getAttribute('content');
        if (orginal_keywords && orginal_keywords.length > 0) {
            if (f) {
                orginal_keywords = keywords_string + "," + orginal_keywords;
            } else {
                orginal_keywords += "," + keywords_string;
            }
        } else {
            orginal_keywords = keywords_string;
        }
        document.querySelector('meta[name="keywords"]').setAttribute('content', orginal_keywords);
    }
}

