import Vue from 'vue'
import Vuex from 'vuex'
import $hub from 'hub-js';
import userobject from "../components/script/userobject";
import USERVISIT from '@/modules/vip'

Vue.use(Vuex);
Vue.prototype.$hub = $hub;

let store = new Vuex.Store({
    state: {
        userList: [],
        user: {},
        init: {},
        user_visit: new USERVISIT('guest'),
        message_notify: undefined
    },
    computed: {
        hasLogin() {
            return store.state.hasLogin;
        },
        is_mobile: function () {
            let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
            return flag;
        },
        is_manager: function () {
            console.log('[is_manager]Vue.prototype.$config.manager_user_ids:', Vue.prototype.$config.manager_user_ids);
            return Vue.prototype.$config.manager_user_ids.indexOf(store.state.user.userinfo.uid) >= 0;
        }
    },
    mutations: {
        addUser(state, username) {
            state.userList.push(username)
        },
        created: function () {
            console.log("store created call");
        },
        login(state, provider) {
            localStorage.setItem('userinfo', JSON.stringify(provider.userinfo));
            if (provider.token) {
                localStorage.setItem('token', provider.token);
            }
            console.log('[setItem]token', provider.token);
            state.hasLogin = true;
            if (provider.token) {
                state.user.token = provider.token;
            }
            state.user.userinfo = userobject.methods.update_userinfo(provider.userinfo);
            state.user_visit = new USERVISIT('guest');
            state.user_visit.is_expire();
            $hub.emit('event:login', {});
        },
        init: function (state, provider) {
            console.log('init provider:', provider)
            state.init.time = provider.time
            state.init.data = provider.data
            $hub.emit('event:init', provider);
            localStorage.setItem('init', JSON.stringify(state.init));
        },
        update_userinfo: function (state, provider) {
            state.message_notify = provider.message_notify;
            $hub.emit('event:update_userinfo', {});
        },
        loginout: function (state, provider) {
            state.hasLogin = false
            state.user.token = undefined
            state.user.userinfo = undefined
            localStorage.removeItem('userinfo')
            localStorage.removeItem('token')
            console.log('call loginout:', state);
            $hub.emit('event:loginout', {});
        }
    },
    actions: {
        init(context) {
            this.dispatch('get_user')
            var init_data = localStorage.getItem('init');
            if (init_data) {
                init_data = JSON.parse(init_data);
                context.state.init = init_data;
            }
            console.log("[init]this.state.user:", this.state.user);
        },
        get_user(context) {
            console.log('get_user:', context);
            try {
                var userinfo = localStorage.getItem('userinfo');
                var token = localStorage.getItem('token');
                if (userinfo) {
                    userinfo = JSON.parse(userinfo)
                }
                console.log('get user:', userinfo, ' token:', token);
                if (token && userinfo) {
                    context.state.hasLogin = true;
                    context.state.user.token = token
                    context.state.user.userinfo = userobject.methods.update_userinfo(userinfo)
                    context.state.user_visit = new USERVISIT('guest');
                    console.log('call get_user:', context.state.user);
                } else {
                    context.state.hasLogin = false;
                    context.state.user.token = '';
                    context.state.user.userinfo = {};
                }
                console.log("[get_user]this.state.user:", this.state.user);
            } catch (e) {
                console.log('get_user err:', e);
            }
            this.state.user_visit.is_expire();

        }
    }
})

export default store