exports.install = function (Vue, options) {
    Vue.prototype.flutter_inappwebview = function (url) {
        return window.flutter_inappwebview;
    };

    Vue.prototype.on_flutter_click = function (e) {
        var flutter_inappwebview = this.flutter_inappwebview();
        console.log('on_flutter_click flutter_inappwebview:', flutter_inappwebview);
        if (flutter_inappwebview) {
            flutter_inappwebview.callHandler('on_click', e);
        }
    }

    Vue.prototype.on_new_flutter_page = function (e) {
        var flutter_inappwebview = this.flutter_inappwebview();
        console.log('on_new_flutter_page flutter_inappwebview:',flutter_inappwebview);
        if (flutter_inappwebview) {
            flutter_inappwebview.callHandler('on_new_flutter_page', e);
        }
    }

    Vue.prototype.call_fullter_created = function () {
        var flutter_inappwebview = this.flutter_inappwebview();
        if (flutter_inappwebview) {
            this.$nextTick(() => {
                flutter_inappwebview.callHandler('handler_created', this.name).then(function (e) {
                    console.log('after handler_created:', e);
                });
            });
        }
    }
};
