import forumobjct from "../../script/forumobjct";

export default {
  name: "list",
  mixins: [forumobjct],
  props: {
    show_index: undefined,
    show_types: undefined
  },
  data() {
    return {
      forums_options: []
    }
  },
  created() {
    this.update_forums_options((this.$forum_conf.forums), this.forums_options);
    console.log('forums_options:', this.forums_options);
    var new_forums_options = [];
    for (let i = 0; i < this.forums_options.length; ++i) {
      var forums_option = this.forums_options[i];
      var add_forum = this.is_add_forum(forums_option);
      if (add_forum) {
        new_forums_options.push(forums_option);
      }
    }
    console.log('new_forums_options:', new_forums_options);
    this.forums_options = new_forums_options;
  },
  methods: {
    on_select: function (forum_conf) {
      console.log('[on_select]forum_conf:', forum_conf);
      this.$emit('on_select_forum', forum_conf);
    },
    is_add_forum: function (forums_option,defalut_types = undefined) {
      var add_forum = true;
      var permision = forums_option['permision'];
      if (permision) {
        if (permision['userdegree']) {
          if (!this.$store.state.hasLogin || permision['userdegree'].indexOf(this.$store.state.user.userinfo.user_degree) < 0) {
            add_forum = false;
          }
        }
      }
      console.log('show_types:', this.show_types);

      if (this.show_types) {
        add_forum = false;
        var types = Object.assign({},forums_option['types']);
        if(!types || Object.keys(types).length == 0){
          types = defalut_types;
        }else{
          if(defalut_types){
            types = Object.assign(types,defalut_types)
          }
        }
        if (types) {
          for (let i = 0; i < this.show_types.length; ++i) {
            var show_type = this.show_types[i];
            if (types[show_type]) {
              add_forum = true;
              break;
            }
          }
        }
      }

      if (add_forum) {
        var list = forums_option['list'];
        if (list) {
          var new_list = [];
          for (let i = 0; i < list.length; ++i) {
            var l = list[i];
            let af = this.is_add_forum(l,defalut_types = forums_option['types']);
            if (af) {
              new_list.push(l);
            }
          }
          forums_option['list'] = new_list;
        }
      }

      return add_forum;
    }
  }
}
