import Vue from "vue";
import lang_confs from '../assets/lang/chinese.json'


Vue.prototype.load_lang = function () {
    if (this.$web_conf.lang == 'chinese') {
        Vue.prototype.lang_confs = lang_confs;
    }
}

var langloader = function (key) {
    if (this.lang_confs) {
        var v = this.lang_confs[key];
        if (!v) {
            console.log('[langloader]未发现:', key)
        }
        return v;
    }
    return "";
}

Vue.prototype.langloader = langloader;
window.$l = Vue.prototype.$l = langloader;