import links from '@/components/common/links'

export default {
  name: 'appfooter',
  components: {
    links
  },
  data() {
    return {}
  },
  computed:{
    app_url:function (){
      var x91app_conf = this.$ads_object.text['x91.18r'];
      if(x91app_conf){
        return x91app_conf['url'];
      }
      return  '/';
    }
  }
}
